
body {
    background-color: rgb(82, 236, 236);
}
.container {
    position: relative;
    width: 25vw;
    height: 90vh;
    border-radius: 15px;
    background-color: rgb(6, 173, 202);
    text-align: center;
    overflow-y: hidden;
    overflow-x: hidden;
}
.todoObject {
    background-color: red;
}
.list {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 80vh;
    overflow-y: auto;

    overflow-x: hidden;
}
.item {
    position: relative;
    width: 80%;
    height: 15vh;
    background-color: lightblue;
    border: 1px solid black;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    margin-top: 15px;
    word-break: break-all;
    

}
.mainContainer {
    min-height: 600px;
    min-width: 580px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1%;

}
.header {
    position: relative;
    display: flex;
    justify-content:space-around;
    align-items: center;
    font-family: cartoon;
    
}

img {
    width: 2vw;
    height: 2vw;
}
img:hover {
    cursor: pointer;
}

.inputBar {
    position: relative; 
    animation: inputBarAnimation;
    animation-duration: 0.5s;
}

input {
    width: 10vw;
    z-index: 1000;
}

.closingInputBar {
    position: relative;
    z-index: -1;
    animation: closingAnimation;
    animation-duration: 0.5s;
}

.goingDown {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 75vh;
    overflow-y: auto;
    overflow-x: hidden;
    animation: inputDown;
    animation-duration: 0.5s;
}
@keyframes inputDown {
    from {height: 80vh;}
    to {height: 75vh;}
}

.goingUp {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 80vh;
    overflow-y: auto;
    overflow-x: hidden;
    animation: inputUp;
    animation-duration: 0.5s;
}
@keyframes inputUp {
    from {height: 75vh;}
    to {height: 80vh;}
}


@keyframes inputBarAnimation {
     from {z-index: -1; bottom: 40px; opacity: 0;}
     to {z-index: 0; bottom: 0px; opacity: 1;}
}


@keyframes closingAnimation {
    from {z-index: 0; bottom: 0px; opacity: 1; }
    to {z-index: -1; bottom: 40px; opacity: 0;}
}
.trashcan {
    position: absolute;
    right: 0;
    top: 0;
    width: 1.5vw;
    padding: 7px;
}
@font-face {
    font-family: cartoon;
    src: url(assets/cartoon.ttf);
}
.inputBarHidden {
    display: none;
}
